/* Header Styles */
header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 80px;
	padding: 0 2rem;
	background-color: var(--color-primary);
	color: var(--color-bg);
  }
  
 .logo img{
	color: var(--color-bg);
	background-color: var(--color-primary-variant);
	width: 100px;
	max-height: auto;
	left: 0;
 }

  nav a{
	margin: 0 2rem;
	color: var(--color-bg);
	text-decoration: none;
  }
  
  nav a:hover {
	color: var(--color-white);
  }

  header .nav-btn{
	padding: 8px;
	cursor: pointer;
	background: var(--color-primary-variant);
	border: none;
	outline: none;
	color: var(--color-bg);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
  }

  @media only screen and (max-width: 1024px){
     
	header .nav-btn{
		visibility: visible;
		opacity: 1;
	}

	header nav {
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--color-primary);
		flex-direction: column;
		transition: var(--transition);
		transform: translateY(-100vh);
	}
     header .responsive__nav{
		transform: none;
	 }

	 nav .nav-close-btn{
       position: absolute;
	   top: 2rem;
	   right: 2rem;
	 }

	 nav a{
		font-size: 1.5rem;
	 }
	 
  }
  
  