.services__cards {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 1.5rem
    
}
.services__card {
	background: var(--color-bg-variant);
	border: 1px solid transparent;
	border-radius: 1rem;
	padding: 2rem;
	text-align: center;
	transition: var(--transition);
	align-items: center;
	
}
.services__card:hover {
	background: transparent;
	border-color: var(--color-primary-variant);
	cursor: pointer;
}
.services__icon{
	color: var(--color-primary);
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

.services__card h5{
	font-size: 0.95rem;
}

@media screen and (max-width: 1024px){
	.sercices__container{
		grid-template-columns: 1fr;
    }
	.sercices__container > div{
		width: 80%;
		padding: 2rem;
		margin: 0 auto;
	}
  }

  /*================= MEDIA QUERIES (SMALL DEVICES) ============== */

  @media screen and (max-width: 600px){
    .services__container{
		gap: 1rem;
    }
	.sercices__container > div{
		width: 100%;
		padding: 2rem 1rem;
		margin: 0 auto;
	}
  }



