
.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: var(--color-bg-variant);
  font-size: 14px;
  margin-top: 2rem;
}

.footer__logo img {
  max-height: auto;
  width: 100px;
  background-color: solid aqua;
}

.footer__social {
  display: flex;
}

.footer__social a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  border-radius: 50%;
  color: var(--color-white);
  background-color: var(--color-primary);
  transition: background-color 0.2s ease-in-out;
}

.footer__social a:hover {
  background-color: var(--color-primary-variant);
}
.footer__social svg {
  width: 20px;
  height: 20px;
}

.footer__copyright {
  margin-top: 50px;
  text-align: center;
  justify-content: center;
  color: #666;
}
@media screen and (max-width: 600px) {
  .footer__social a {
    flex-direction: column;
    text-align: center;
    gap: 1.2rem;
  }

  .footer__logo {
    margin-bottom: 20px;
  }

  .footer__social {
    margin-bottom: 20px;
  }

  .footer__social a {
    margin-right: 0;
    margin-bottom: 10px;
  }
}



  

  